<template>
	<div>
		<vue-headful title="Register – FESTIVAL Pack'd" />
		<div class="container">
			<div class="row justify-content-around">
				<div class="col-md-4">
					<nav>
						<div class="nav-logo pb-0">
							<div class="nav-link">
								<img src="img/packr-icon.svg">
								<span class="ml-2">FESTIVAL Pack'd</span>
							</div>
						</div>
					</nav>
					<hr>
					<form name="registerForm" novalidate>
						<div class="form-group">
							<label>Username</label>
							<input type="text" class="form-control" :class="{'invalid': ($v.user.username.$dirty && !$v.user.username.required) || (authData && authData.status == 409)}" placeholder="John Appleseed" v-model="user.username" @input="$v.user.username.$touch()">
							
							<span class="text-danger" v-if="$v.user.username.$dirty && !$v.user.username.required" >Requried field</span>
							<span class="text-danger" v-if="authData && authData.status == 409">Username already in use</span>
						</div>
						
						<div class="form-group mt-5">
							<label>E-Mail Address</label>
							<input type="email" class="form-control" :class="{'invalid': ($v.user.email.$dirty && !$v.user.email.required) || (authData && authData.status == 409)}" placeholder="johnappleseed@example.com" v-model="user.email" @input="$v.user.email.$touch()">
							
							<span class="text-danger" v-if="$v.user.email.$dirty && !$v.user.email.required">Requried field</span>
							<span class="text-danger" v-if="$v.user.email.$dirty && !$v.user.email.email">Invalid E-Mail address</span>
							<span class="text-danger" v-if="authData && authData.status == 409">E-Mail address already in use</span>
						</div>
						<div class="form-group">
							<label>Confirm E-Mail Address</label>
							<input type="email" class="form-control" :class="{'invalid': $v.user.emailConf.$dirty && !$v.user.emailConf.required}" placeholder="johnappleseed@example.com" v-model="user.emailConf" @input="$v.user.emailConf.$touch()">
							
							<span class="text-danger" v-if="$v.user.emailConf.$dirty && !$v.user.emailConf.required">Requried field</span>
							<span class="text-danger" v-if="$v.user.emailConf.$dirty && !$v.user.emailConf.sameAsEmail">E-Mail addresses do not match</span>
						</div>
						
						<div class="form-group mt-5">
							<label>Password</label>
							<input type="password" class="form-control" :class="{'invalid': $v.user.password.$dirty && !$v.user.password.required}" placeholder="Required" v-model="user.password" @input="$v.user.password.$touch()">
							
							<span class="text-danger" v-if="$v.user.password.$dirty && !$v.user.password.required">Requried field</span>
							<span class="text-danger" v-if="$v.user.password.$dirty && !$v.user.password.minLength">Password too short</span>
						</div>
						<div class="form-group">
							<label>Confirm Password</label>
							<input type="password" class="form-control" :class="{'invalid': $v.user.passwordConf.$dirty && !$v.user.passwordConf.required}" placeholder="Required" v-model="user.passwordConf" @input="$v.user.passwordConf.$touch()">
							
							<span class="text-danger" v-if="$v.user.passwordConf.$dirty && !$v.user.passwordConf.required">Requried field</span>
							<span class="text-danger" v-if="$v.user.passwordConf.$dirty && !$v.user.passwordConf.sameAsPassword">Passwords do not match</span>
						</div>
					</form>
					
					<div class="row mt-5 d-none d-md-flex">
						<div class="col col-auto">
							<button class="btn btn-primary" @click="register()" :disabled="$v.user.$invalid || isWorking">Register</button>
						</div>
						
						<div class="col text-right" v-show="!isWorking">
							<router-link tag="button" class="btn btn-link mt-2 p-0" to="/login">Already have an account?</router-link>
						</div>
						<div class="col text-right" v-show="isWorking">
							<div class="loading-indicator" />
						</div>
					</div>
					
					<div class="row mt-5 d-md-none">
						<div class="col col-12" v-show="!isWorking">
							<button class="btn btn-block btn-primary" @click="register()" :disabled="$v.user.$invalid || isWorking">Register</button>
						</div>
						
						<div class="col col-12 mt-3 text-center" v-show="!isWorking">
							<router-link tag="button" class="btn btn-link mt-2 p-0" to="/login">Already have an account?</router-link>
						</div>
						<div class="col col-12 text-center" v-show="isWorking">
							<div class="loading-indicator" />
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<style lang="less" scoped>
.form-group span {
	display: block;
}
.loading-indicator {
	margin-top: 7px;
}
</style>

<script>
import crypto from 'crypto-js';
import { AuthAPI } from "@/scripts/ApiUtil";
import { required, email, sameAs, minLength } from 'vuelidate/lib/validators'

export default {
	name: 'register',
	data: () => {
		return {
			user: {
				username: "",
				email: "",
				emailConf: "",
				password: "",
				passwordConf: ""
			},
			
			isWorking: false,
			authData: null
		}
	},
	validations: {
		user: {
			username: { required },
			email: { required, email },
			emailConf: { required, sameAsEmail: sameAs('email') },
			password: { required, minLength: minLength(8) },
			passwordConf: { required, sameAsPassword: sameAs('password') }
		}
	},
	async created() {
		if (this.$cookies.get("authToken")) {
			if (this.$route.query.next) {
				this.$router.replace(this.$route.query.next);
			} else {
				this.$router.replace("/");
			}
		}
	},
	mounted() {
		window.scrollTo(0,0);
	},
	methods: {
		async register() {
			this.isWorking = true;
			let _authData = await AuthAPI.register({
				username: this.user.username,
				email: this.user.email,
				password: crypto.SHA256(this.user.password).toString(crypto.enc.Hex),
			});
			
			if (_authData.status == 200) {
				// For some reason, we need this while we are in development mode
				this.$cookies.set("authToken", _authData.data["token"], (() => {
					const expiryDate = new Date();
					expiryDate.setHours(expiryDate.getHours() + 2);
					return expiryDate;
				})());
				
				this.$router.replace("/");
			} else {
				this.authData = {
					status: _authData.status,
					message: _authData.data.message
				}
				this.isWorking = false;
				
				if (_authData.status == 500) {
					bootbox.alert(`There was an error creating your account:<br>${_authData.data.message}`);
				}
			}
		}
	}
}
</script>
